import React from "react";
import { Composition } from "atomic-layout";
import { Text, Image, Card, Link } from "../components";
import styled from "styled-components";

const templateMobile = `
  thumbnail
  heading
  actions
`;

const templateTablet = `
  thumbnail heading
  thumbnail actions
`;

const Title = styled.h3`
  color: #000;
`;

const Flat = ({ title, description, imageUrl, url, view }) => (
  <Card>
    <Composition
      template={templateMobile}
      templateMd={templateTablet}
      templateLg={templateMobile}
      templateColsMdOnly="minmax(100px, 1fr) 1fr"
      padding={15}
      gutter={15}
      gutterLg={25}
    >
      {({ Thumbnail, Heading }) => (
        <>
          {/* <Thumbnail>
            <Image src={imageUrl} alt={title} loading="lazy" />
          </Thumbnail> */}
          <Heading>
            <Title>{title}</Title>
            <Text small muted>
              {description}
            </Text>
            <br />
            <Link
              href={url}
              target="_blank"
              rel="noopener noreferrer"
              link
              muted
            >
              <u>{view}</u>
            </Link>
          </Heading>
        </>
      )}
    </Composition>
  </Card>
);

export default Flat;
